import './App.scss';
import { HashRouter, Route } from 'react-router-dom';

import Header from './components/Header';
import StockAvg from './components/StockAvg';
import CagrCalculator from './components/CagrCalculator';
import PercentageCalculator from "./components/PercentageCalculator";
import NumberToWord from './components/NumberToWord';
import About from './components/About';
import Footer from './components/Footer';

function App() {
  return (
    <HashRouter basename='/'>
      <Header />
      <Route path='/' exact component={StockAvg} />
      <Route path='/cagr-calculator' exact component={CagrCalculator} />
      <Route path='/percentage-calculator' exact component={PercentageCalculator} />
      <Route path='/number-to-word' exact component={NumberToWord} />
      <Route path='/about' component={About} />
      <Footer />
    </HashRouter>
  );
}

export default App;
