import { Link, useLocation } from "react-router-dom";
import { Navbar, Nav, Container} from 'react-bootstrap';

const Header = () => {
  return (
    <Navbar collapseOnSelect>
      <Container>
        <Navbar.Brand>Projects</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />

        <Navbar.Collapse className='menu'>
          <Nav>
            <li><Link to="/">Stock average calculator</Link></li>
            <li><Link to="/cagr-calculator">CAGR calculator</Link></li>
            <li><Link to="/percentage-calculator">Percentage calculator</Link></li>
            <li><Link to="/number-to-word">Number to word</Link></li>
            <li><Link to="/about">About</Link></li>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  )
}

export default Header
