import { useState } from 'react'
import { Card, Form, Row, Button} from "react-bootstrap";
import RowCol from '../lib/RowCol';

const NumberToWord = () => {
  const [allKeys, setAllKeys] = useState({
    numberValue: 0, wordValue: "zero"
  })

  const changeHandler = e => {
    setAllKeys( prevValues => {
      return { ...prevValues, [e.target.name]: e.target.value }
    })

    setAllKeys( prevValues => {
      return { ...prevValues, wordValue: numToWords(+prevValues.numberValue) }
    })
  }

  const handleSubmit = (e) => {
    e.preventDefault()

    setAllKeys( prevValues => {
      return { ...prevValues, wordValue: numToWords(+prevValues.numberValue) }
    })
  }

  const handleClear = (e) => {
    e.preventDefault()

    setAllKeys({
      numberValue: 0, wordValue: "zero"
    });
  }

  const arr = x => Array.from(x);
  const num = x => Number(x) || 0;
  const isEmpty = xs => xs.length === 0;
  const take = n => xs => xs.slice(0,n);
  const drop = n => xs => xs.slice(n);
  const reverse = xs => xs.slice(0).reverse();
  const comp = f => g => x => f (g (x));
  const not = x => !x;
  const chunk = n => xs =>
    isEmpty(xs) ? [] : [take(n)(xs), ...chunk (n) (drop (n) (xs))];
  
  const a = [
    '', 'one', 'two', 'three', 'four',
    'five', 'six', 'seven', 'eight', 'nine',
    'ten', 'eleven', 'twelve', 'thirteen', 'fourteen',
    'fifteen', 'sixteen', 'seventeen', 'eighteen', 'nineteen'
  ];
  
  const b = [
    '', '', 'twenty', 'thirty', 'forty',
    'fifty', 'sixty', 'seventy', 'eighty', 'ninety'
  ];
  
  const g = [
    '', 'thousand', 'million', 'billion', 'trillion', 'quadrillion',
    'quintillion', 'sextillion', 'septillion', 'octillion', 'nonillion'
  ];

  let numToWords = n => {  
    let makeGroup = ([ones,tens,huns]) => {
      return [
        num(huns) === 0 ? '' : a[huns] + ' hundred ',
        num(ones) === 0 ? b[tens] : (b[tens] && b[tens] + '-' || ''),
        a[tens+ones] || a[ones]
      ].join('');
    };
    
    let thousand = (group,i) => group === '' ? group : `${group} ${g[i]}`;
    
    if (typeof n === 'number')
      return numToWords(String(n));
    else if (n === '0')
      return 'zero';
    else
      return comp (chunk(3)) (reverse) (arr(n))
        .map(makeGroup)
        .map(thousand)
        .filter(comp(not)(isEmpty))
        .reverse()
        .join(' ');
  };

  return (
    <div className="container">
      <Card border="success">
        <Card.Header as="h5">Number to word</Card.Header>
        <Card.Body>
          <Form onSubmit={handleSubmit}>
            <Row>
              <RowCol text="Number" name="numberValue" value={allKeys.numberValue} size="4" onChangeHandler={changeHandler} />
            </Row>
            <Button variant="success" type="submit">Convert</Button>{' '}
            <Button variant="warning" type="reset" onClick={handleClear}>Clear</Button>
          </Form>
        </Card.Body>
        <Card.Footer className="text-success text-result">{allKeys.wordValue}</Card.Footer>
      </Card>
    </div>
  )
}

export default NumberToWord