import { useState } from 'react'
import { Card, Form, Row, Button} from "react-bootstrap";
import RowCol from '../lib/RowCol';

const CagrCalculator = () => {
  const [allKeys, setAllKeys] = useState({
    firstValue: 0, finalValue: 0,
    noOfYears: 0, cagr: 0
  })
  
  const changeHandler = e => {
    setAllKeys( prevValues => {
      return { ...prevValues, [e.target.name]: e.target.value }
    })

    setAllKeys( prevValues => {
      if (+prevValues.noOfYears > 0 && +prevValues.finalValue > 0 && +prevValues.firstValue > 0) {
        return { ...prevValues,
          cagr: (Math.pow((+prevValues.finalValue / +prevValues.firstValue), (1/+prevValues.noOfYears)) - 1) * 100
        }
      } else {
        return { ...prevValues, [e.target.name]: e.target.value }
      }
    });
  }

  const handleSubmit = (e) => {
    e.preventDefault()

    if (allKeys.firstValue <= 0) {
      alert('Please enter a Initial Value')
      return
    }

    if (allKeys.finalValue <= 0) {
      alert('Please enter a Final Value')
      return
    }

    if (allKeys.noOfYears <= 0) {
      alert('Please enter a no of years')
      return
    }

    setAllKeys( prevValues => {
      return { ...prevValues,
        cagr: (Math.pow((+prevValues.finalValue / +prevValues.firstValue), (1/+prevValues.noOfYears)) - 1) * 100
      }
    });
  }

  const handleClear = (e) => {
    e.preventDefault()

    setAllKeys({
      firstValue: 0, finalValue: 0,
      noOfYears: 0, cagr: 0
    });
  }

  return (
    <div className="container">.
      <Card border="success">
        <Card.Header as="h5">CAGR calculator</Card.Header>
        <Card.Body>
          <Form onSubmit={handleSubmit}>
            <Row>
              <RowCol text="Initial Value" name="firstValue" value={allKeys.firstValue} size="4" onChangeHandler={changeHandler} />
              <RowCol text="Final Value" name="finalValue" value={allKeys.finalValue} size="4" onChangeHandler={changeHandler} />
              <RowCol text="Years" name="noOfYears" value={allKeys.noOfYears} size="4" onChangeHandler={changeHandler} />
            </Row>
            <Button variant="success" type="submit">Calculate CAGR</Button>{' '}
            <Button variant="warning" type="reset" onClick={handleClear}>Clear</Button>
          </Form>
        </Card.Body>
        <Card.Footer className="text-success text-result">CAGR {allKeys.cagr}%</Card.Footer>
      </Card>
    </div>
  )
}

export default CagrCalculator
