import {Form, Col} from "react-bootstrap";

const RowCol = ({text, name, value, size, onChangeHandler}) => {

  return (
    <Col lg={size} xs={12}>
      <Form.Group controlId={name}>
        <Form.Label>{text}</Form.Label>
        <Form.Control
          type='number'
          placeholder={text}
          name={name}
          value={value}
          onChange={onChangeHandler}
        />
      </Form.Group>
    </Col>
  )
};

RowCol.defaultProps = {
  value: 0,
  size: 6
}


export default RowCol