import { useState } from 'react'
import { Card, CardGroup, Form, Row, Button } from "react-bootstrap";
import RowCol from '../lib/RowCol';

const StockAvg = () => {
  const [allValues, setAllValues] = useState({
    priceOne: 0, qtyOne: 0,
    priceTwo: 0, qtyTwo: 0,
    totalInvestment: 0, totalQty: 0
  })
  
  const changeHandler = e => {
    setAllValues( prevValues => {
      return { ...prevValues, [e.target.name]: e.target.value }
    })

    setAllValues( prevValues => {
      return { ...prevValues,
        totalInvestment: (+prevValues.priceOne * +prevValues.qtyOne) + (+prevValues.priceTwo * +prevValues.qtyTwo),
        totalQty: +prevValues.qtyOne + +prevValues.qtyTwo,
      }
    });
  }

  const handleSubmit = (e) => {
    e.preventDefault()

    if (allValues.priceOne <= 0 || allValues.priceTwo <= 0) {
      alert('Please add a price')
      return
    }

    if (allValues.qtyOne <= 0 || allValues.qtyTwo <= 0) {
      alert('Please add a Quantity')
      return
    }

    setAllValues( prevValues => {
      return { ...prevValues,
        totalInvestment: (+prevValues.priceOne * +prevValues.qtyOne) + (+prevValues.priceTwo * +prevValues.qtyTwo),
        totalQty: +prevValues.qtyOne + +prevValues.qtyTwo,
      }
    });
  }

  const handleClear = (e) => {
    e.preventDefault()

    setAllValues({
      priceOne: 0, qtyOne: 0,
      priceTwo: 0, qtyTwo: 0,
      totalInvestment: 0, totalQty: 0
    });
  }
  
  return (
    <div className="container">
      <Card border="success">
        <Card.Header as="h5">Stock average calculator</Card.Header>
        <Card.Body>
          <Form onSubmit={handleSubmit}>
            <CardGroup>
              <Card>
                <Card.Header as="h5">First Buy</Card.Header>
                <Card.Body>
                  <Row>
                    <RowCol text="Buy Price" name="priceOne" value={allValues.priceOne} size="6" onChangeHandler={changeHandler} />
                    <RowCol text="Buy Quantity" name="qtyOne" value={allValues.qtyOne} size="6" onChangeHandler={changeHandler} />
                  </Row>
                </Card.Body>
                <Card.Footer className="text-muted">Total Investment {allValues.priceOne * allValues.qtyOne}</Card.Footer>
              </Card>
              <Card>
                <Card.Header as="h5">Second Buy</Card.Header>
                <Card.Body>
                  <Row>
                    <RowCol text="Buy Price" name="priceTwo" value={allValues.priceTwo} size="6" onChangeHandler={changeHandler} />
                    <RowCol text="Buy Quantity" name="qtyTwo" value={allValues.qtyTwo} size="6" onChangeHandler={changeHandler} />
                  </Row>
                </Card.Body>
                <Card.Footer className="text-muted">Total Investment {allValues.priceTwo * allValues.qtyTwo}</Card.Footer>
              </Card>
            </CardGroup>
            <div className="btn-m-1r">
              <Button variant="success" type="submit">Calculate Average</Button>{' '}
              <Button variant="warning" type="reset" onClick={handleClear}>Clear</Button>
            </div>
          </Form>
        </Card.Body>
        <Card.Footer className="text-success">
          <p>
            <span>Total Investment { allValues.totalInvestment},  </span>
            <span>Total Quantity { allValues.totalQty }  </span>
          </p>
          <p className="text-result">Average { allValues.totalQty > 0 ? (allValues.totalInvestment / allValues.totalQty) : 0} </p>
        </Card.Footer>
      </Card>
    </div>
  )
}

export default StockAvg
