import { useState } from 'react'
import { Card, Form, Row, Button, Col, InputGroup, CardGroup } from "react-bootstrap";
import RowCol from '../lib/RowCol';

const PercentageCalculator = () => {
  const [allKeys, setAllKeys] = useState({
    percentage: 0, amount: 0, result: 0,
    amountOne: 0, amountTwo: 0, result2: 0
  })
  
  const changeHandler = e => {
    setAllKeys( prevValues => {
      return { ...prevValues, [e.target.name]: e.target.value }
    })

    setAllKeys( prevValues => {
      if (+prevValues.percentage > 0 && +prevValues.amount > 0) {
        return { ...prevValues,
          result: +prevValues.percentage > 0 && +prevValues.amount > 0 ? (+prevValues.amount * +prevValues.percentage) / 100 : 0,
          result2: +prevValues.amountOne > 0 && +prevValues.amountTwo > 0 ? (+prevValues.amountOne / +prevValues.amountTwo) * 100 : 0
        }
      } else {
        return { ...prevValues, [e.target.name]: e.target.value }
      }
    });
  }

  const handleSubmit = (e) => {
    e.preventDefault()

    if (+allKeys.percentage <= 0) {
      alert('Please enter a percentage')
      return
    }

    if (+allKeys.amount <= 0) {
      alert('Please enter a Amount')
      return
    }

    if (+allKeys.percentage > 0 && +allKeys.amount > 0) {
      setAllKeys( prevValues => {
        return { ...prevValues,
          result: (+prevValues.amount * +prevValues.percentage) / 100
        }
      });
    } 
    
    if(+allKeys.amountOne > 0 && +allKeys.amountTwo > 0){
      setAllKeys( prevValues => {
        return { ...prevValues,
          result2: (+prevValues.amountOne / +prevValues.amountTwo) * 100
        }
      });
    }
  }

  const handleClear = (e) => {
    e.preventDefault()

    setAllKeys({
      percentage: 0, amount: 0, result: 0,
      amountOne: 0, amountTwo: 0, result2: 0
    });
  }

  return (
    <div className="container">
      <Card border="success">
        <Card.Header as="h5">Percentage calculator</Card.Header>
        <Card.Body>
          <Form onSubmit={handleSubmit}>
            <CardGroup>
              <Card>
                <Card.Body>
                  <Row>
                    <Col lg={6} xs={12}>
                      <Form.Group controlId="firstValue">
                        <Form.Label>What is</Form.Label>
                        <InputGroup className="mb-2">
                          <Form.Control
                            type='number'
                            name='percentage'
                            value={allKeys.percentage}
                            onChange={changeHandler}
                          />
                          <InputGroup.Prepend>
                            <InputGroup.Text>%</InputGroup.Text>
                          </InputGroup.Prepend>
                        </InputGroup>
                      </Form.Group>
                    </Col>
                    <RowCol text="of" name="amount" value={allKeys.amount} size="6" onChangeHandler={changeHandler} />
                  </Row>
                </Card.Body>
              </Card>
              <Card>
                <Card.Body>
                  <Row>
                    <RowCol text="amount" name="amountOne" value={allKeys.amountOne} size="6" onChangeHandler={changeHandler} />
                    <RowCol text="is what % of" name="amountTwo" value={allKeys.amountTwo} size="6" onChangeHandler={changeHandler} />
                  </Row>
                </Card.Body>
              </Card>
            </CardGroup>
            <div className="btn-m-1r">
              <Button variant="success" type="submit">Calculate</Button>{' '}
              <Button variant="warning" type="reset" onClick={handleClear}>Clear</Button>
            </div>
          </Form>
        </Card.Body>
        <Card.Footer className="text-success text-result">
          <p>{allKeys.result !== 0 ? (allKeys.result + " is " + allKeys.percentage + "% of " + allKeys.amount) : ""}</p>
          <p>{allKeys.result2 !== 0 ? (allKeys.amountOne + " is " + allKeys.result2 + "% of " + allKeys.amountTwo) : ""}</p>
        </Card.Footer>
      </Card>
    </div>
  )
}

export default PercentageCalculator
